:root {
  /* Colors */
  --pink: #ffd5d8;
  --brown: #58311f;
  --yellow: #d4ca6f;
  --lightyellow: #fdf9d5;

  --primary: #318bb4;
  --secondary: #f98e11;
  --tertiary: #618c06;
  --quadary: #fac301;
  --superlightquadary: #fce180;

  --white: #fffffe;
  --white-transparent-heavy: rgba(255, 255, 255, 0.9);
  --white-transparent-medium: rgba(255, 255, 255, 0.5);
  --white-transparent-light: rgba(255, 255, 255, 0.2);
  --white-transparent-superlight: rgba(255, 255, 255, 0.05);
  --black: #232323;
  --darkblack: #171717;
  --very-darkblack: #010101;
  --black-transparent: rgba(0, 0, 0, 0.9);
  --black-transparent-medium: rgba(0, 0, 0, 0.5);
  --black-transparent-light: rgba(0, 0, 0, 0.2);
  --black-transparent-superlight: rgba(0, 0, 0, 0.025);
  --lightgray: #f3f3f3;
  --gray: #505050;

  /* Spacing */
  --space-small: 0.5rem;
  --space-regular: 1rem;
  --space-medium: 2rem;
  --space-big: 3rem;

  /* Durations */
  --superquick: 0.1s;
  --quick: 0.2s;
  --slow: 0.4s;
  --superslow: 0.6s;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fffffe;
  background-color: var(--white);
  color: #232323;
  color: var(--black);
  line-height: 1.5;
  -webkit-hyphens: auto;
          hyphens: auto;
  -webkit-hyphenate-character: "-";
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
  border-left: none;
  font-weight: 600;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
  letter-spacing: 0.1rem;
}
h1,
h2 {
  letter-spacing: 0.2rem;
  word-wrap: break-word;
  overflow-wrap: break-word;
  word-break: break-word;
}
@media screen and (max-width: 720px) {
  h1 {
    font-size: 1.75rem;
  }
}
.with-border-left {
  padding: 1rem 1.5rem;
  border-left: 0.5rem solid #fac301;
  border-left: 0.5rem solid var(--quadary);
}

hr {
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid var(--black-transparent-light);
  width: 70%;
}
hr.full-width {
  width: 100%;
}

button {
  color: #232323;
  color: var(--black);
}

img {
  max-width: 100%;
}

ul {
  list-style-type: square;
}
ul.link-list li {
  padding: 0.3rem 0;
}

iframe {
  border: 0;
}

.SpotifyPlayer {
  width: 100%;
  max-width: 100%;
  height: 400px;
  margin: 1rem 0;
}

.info-box {
  padding: 1rem;
  background-color: #fac301;
  background-color: var(--quadary);
}

.flippable {
  transition: -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.flippable.flipped {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}

figcaption {
  font-style: italic;
  font-size: 0.9em;
  text-align: right;
}

